import React, { Component } from 'react';
// import logo from './logo.svg';
import './App.css';
import Home from './containers/Home';
import Blockchain from './containers/Blockchain';
import { Route, Link } from 'react-router-dom';

class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      username: '',
      publicKey: '',
      privateKey: ''
    };
  }

  componentDidMount() {
    const username = localStorage.getItem('username');
    const publicKey = localStorage.getItem('publicKey');
    const privateKey = localStorage.getItem('privateKey');

    console.log(this.props.history)
    if (!username) {
      this.props.history.replace('/home');
      return;
    }

    if (!publicKey || !privateKey)  {
      // TODO: Generate Private Key and Public Key
      localStorage.setItem('publicKey', '1234publickey');
      localStorage.setItem('privateKey', '1234privatekey');
    }

    this.setState(
      username,
      publicKey,
      privateKey
    );

    this.props.history.replace('/blockchain');
  }

  render() {
    return (
      <div className="app">
        <Route path="/home" component={Home} />
        <Route path="/blockchain" component={Blockchain} />
      </div>
    );
  }
}

export default App;
